import './App.css';
import Signin from './Signin'
import Profile from './Profile';
import {  HashRouter, Route, Switch } from 'react-router-dom';


function App() {

  const token = localStorage.getItem("accessToken");

  if(!token) {
    return <Signin />
  }

  return (
    <div className="wrapper">
      <HashRouter basename={'/checkspf-demo'}>
       <Switch>
         <Route path="/">
           <Profile/>
         </Route>
         <Route path="/profile">
           <Profile/>
         </Route>
       </Switch>
       </HashRouter> 
    </div>
  );
}

export default App;
